*,
::before,
::after {
  box-sizing: border-box;
  outline: none;
}

html {
  font-family: "Opensans", sans-serif;
  scroll-behavior: smooth;
}

body {
  font-size: 16px;
  margin: 0;
  padding: 0;
  color: var(--text-color-black);
}

.visually-hidden {
  position: absolute !important;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
  width: 1px !important;
  height: 1px !important;
  padding: 0 !important;
  border: 0 !important;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  object-fit: cover;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

p {
  margin: 0;
  color: var(--text-color-black);
  line-height: 1.75;
}

h2 {
  padding: 0;
  margin: 0;
  font-size: 42px;
  line-height: 1.1;
  font-weight: 900;
  color: var(--text-color-black);
}

.container {
  max-width: 1135px;
  margin: 0 auto;
  padding: 0 15px;
}

.btn-reset {
  font-family: "Opensans", sans-serif;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.red-text {
  color: var(--text-color-red);
}

.grey-text {
  color: var(--text-color-grey);
}

.detailes-link-wrapper {
  text-align: right;
}

.detailes-link {
  font-size: 18px;
  color: #005699;
  font-weight: 600;
  line-height: 2.5;
}

.section-border {
  border-bottom: 13px solid #ddd9b3;
}

.header {
  margin-bottom: 95px;
}

.header-top {
  border-bottom: 2px solid #eaeaea;
}

.header-top__container {
  display: flex;
  justify-content: space-between;
  padding: 31px 0 17px 0;
}

.logo-link {
  display: flex;
  align-items: center;
}

.logo-link__name-wrapper {
  display: flex;
  flex-direction: column;
}

.logo-link__img {
  margin-right: 13px;
}

.logo-link__subname {
  display: inline-block;
  font-size: 30px;
  font-weight: 700;
  margin-bottom: 10px;
}

.logo-link__name {
  font-size: 36px;
  font-weight: 900;
}

.header-price {
  display: flex;
}

.header-price__btn {
  display: inline-block;
  width: 14px;
  max-height: 16px;
  margin-top: 17px;
  margin-right: 24px;
}

.burger-line {
  width: 100%;
  height: 2px;
  background-color: #1a171b;
}

.burger-line:not(:first-child) {
  transition: transform 0.3s ease;
}

.burger-line:not(:last-child) {
  margin-bottom: 4px;
  transition: transform 0.3s ease;
}

.header-price__wrapper {
  display: flex;
  flex-direction: column;
}

.header-price__item {
  padding-left: 32px;
  font-weight: 700;
  background-repeat: no-repeat;
  background-position: left center;
}

.header-price__item:first-child {
  margin-bottom: 11px;
  background-image: url(../img/images/calc.png);
}

.header-price__item:last-child {
  background-image: url(../img/images/wallet.png);
}

.header-social__list {
  display: flex;
  margin-bottom: 9px;
}
.header-social__list .header-social__item:not(:last-child) {
  margin-right: 6px;
}

.header-social__tel {
  display: inline-block;
  font-weight: 700;
  color: var(--text-color);
  margin-bottom: 10px;
}

.header-social__text {
  font-size: 12px;
}
.header-social__text span {
  display: block;
}
.header-social__text span:first-child {
  font-weight: 700;
}
.header-social__text span:last-child {
  font-weight: 300;
}

.header-bottom {
  padding-top: 23px;
}

.header-bottom__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-bottom__link {
  font-size: 16px;
  font-weight: 800;
  color: var(--text-color-black);
  transition: color 0.3s ease;
}

.header-bottom__link:hover {
  color: var(--text-color-red);
  transition: color 0.3s ease;
}

.hero-section {
  margin-bottom: 18px;
}

.price-wrapper {
  text-align: center;
  margin-bottom: 55px;
}

.price {
  display: block;
  font-size: 70px;
  line-height: 1;
  font-weight: 800;
  color: var(--text-color-red);
}
.price span {
  display: inline-block;
  color: #fff;
  padding: 10px;
  border-radius: 15px;
  background-color: var(--text-color-red);
}

